"use client";

import { FC, useEffect, useRef, useState } from 'react';
import { TextWrapper } from '@/components/TextWrapper';
import { useTranslation } from '@/lib/i18n/client';
import ThreeDotsImage from "@/public/images/threeDots.svg";
import Image from 'next/image';
import { TabGroup, TabPanel, TabPanels } from '@headlessui/react';
import { Template, Props as TemplateProps } from "./Template"

interface Props {
    lng: string;
}

export const WhyLomTech: FC<Props> = ({ lng }) => {
    const { t } = useTranslation(lng, 'common');

    const whyTextRef = useRef<HTMLDivElement>(null);
    const whySARef1 = useRef<HTMLDivElement>(null);
    const whySARef2 = useRef<HTMLDivElement>(null);

    useEffect(() => {
        const handleScroll = () => {
            const handleVisibility = (ref: React.RefObject<HTMLDivElement | null>, className: string) => {
                if (ref?.current) {
                    const rect = ref.current.getBoundingClientRect();
                    if (rect.top <= window.innerHeight && rect.bottom >= 0) {
                        ref.current.classList.add(className);
                    } else {
                        ref.current.classList.remove(className);
                    }
                }
            };

            handleVisibility(whyTextRef, 'why-text-visible');
            handleVisibility(whySARef1, 'why-sa-visible');
            handleVisibility(whySARef2, 'why-sa-visible');
        };

        window.addEventListener('scroll', handleScroll);
        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, []);


    const services = [
        'costEffective',
        'expertiseAndInnovation',
        'clientCentricApproach',
        'strategicLocation',
    ]

    return (
        <div id="whylomtech" className="relative px-4 py-12 md:px-16 md:py-24" style={{ backgroundColor: '#0d3388' }}>

            <h2 className="Section2__Heading-sc-1m3n9it-3 fiBlEe text-3xl sm:text-4xl font-bold leading-tight text-white text-center mb-6">
                {t('sectionHeading')}
            </h2>

            <Image src={ThreeDotsImage} alt="" width={5} height={5} className="mx-auto mb-3" />

            <TabGroup className="w-full flex flex-col md:flex-row md:justify-around md:items-center" >
                <TabPanels className="w-full md:w-1/2 xl:max-w-lg">
                    <TabPanel>
                        <div className="max-w-xl text-left relative">
                            <div ref={whyTextRef} className="relative why-text-container">
                                <p className="text-2xl font-semibold text-blue-500 why-text-animation">
                                    <span>{t('whyLomTech.default.title')}</span>
                                </p>
                            </div>


                            <p className="text-3xl sm:text-4xl font-bold leading-tight text-white mt-4" style={{ maxWidth: '100%', lineHeight: '2.5rem' }}>
                                {t('whyLomTech.default.offerText')}
                            </p>

                            <p className="text-xl sm:text-xl font-bold leading-tight text-gray-400 mt-4" style={{ maxWidth: '100%', lineHeight: '2.5rem' }}>
                                <span>{t('whyLomTech.default.connectText')}</span>
                                <span ref={whySARef1} className="relative why-sa-container">
                                    <span className="text-white why-sa-animation">{t('whyLomTech.default.highLightText')}</span>
                                </span>
                                <span>
                                    {t('whyLomTech.default.problemSolvedText')}
                                </span>

                                <span ref={whySARef2} className="relative block why-sa-container">
                                    <span className="text-white why-sa-animation">LOMTech</span>
                                </span>
                            </p>
                        </div>
                    </TabPanel>
                    {
                        services.map((service) => {
                            const props: TemplateProps = {
                                title: t(`whyLomTech.${service}.title`),
                                points: [
                                    { title: t(`whyLomTech.${service}.point1Title`), description: t(`whyLomTech.${service}.point1Text`) },
                                    { title: t(`whyLomTech.${service}.point2Title`), description: t(`whyLomTech.${service}.point2Text`) },
                                ]
                            }
                            return (<TabPanel key={props.title}>
                                <Template {...props} />
                            </TabPanel>)
                        })
                    }
                </TabPanels>
                <TextWrapper lng={lng} />
            </TabGroup>
        </div>
    );
};