'use client';

import React from 'react';
import { ArrowBounce } from './ArrowBounce';

export const BgAnimation: React.FC = () => {
  const arrowImage = '/images/arrow.png';
  const videoFile = '/videos/landing.mp4';

  const scrollToSection = () => {
    const section = document.getElementById('whylomtech');
    if (section) {
      section.scrollIntoView({ behavior: 'smooth' });
    }
  };

  return (
    <div id="animation" className="relative w-full h-screen overflow-hidden">
      <video
        className="absolute top-0 left-1/2 transform -translate-x-1/2 w-full h-full object-cover"
        autoPlay
        loop
        muted
        playsInline
        style={{
          height: '100vh',
          width: '100vw', 
          objectFit: 'cover', 
        }}
      >
        <source src={videoFile} type="video/mp4" />
        Your browser does not support the video tag.
      </video>
      <div
        onClick={scrollToSection}
        className="absolute bottom-5 left-1/2 transform -translate-x-1/2 flex flex-col space-y-5"
      >
        <ArrowBounce arrowImage={arrowImage} bottomPosition="5px" />
        <ArrowBounce arrowImage={arrowImage} bottomPosition="20px" />
        <ArrowBounce arrowImage={arrowImage} bottomPosition="35px" />
      </div>
    </div>
  );
};
