import React from 'react';
import Image from 'next/image';
import Link from 'next/link';
import { useTranslation } from 'react-i18next';

interface PortfolioCardProps {
  href: string;
  imageSrc: string;
  title: string;
  client: string;
  location: string;
}

const PortfolioCard: React.FC<PortfolioCardProps> = ({ href, imageSrc, title, client, location }) => {
  const { t } = useTranslation('common');

  return (
    <Link href={href} className="block bg-white rounded-lg overflow-hidden hover:shadow-xl transition-shadow duration-300">
      <div className="relative w-full h-48">
        <Image
          src={imageSrc}
          alt={title}
          fill
          style={{ objectFit: 'cover' }}
          className="w-full h-48 object-cover"
        />
      </div>
      <div className="p-2">
        <h3 className="text-lg font-semibold mb-1">{title}</h3>
        <p className="text-sm text-gray-500 mb-1">{t('portfolio.projects.client')}: {client}</p>
        <p className="text-sm text-gray-500">{t('portfolio.projects.location')}: {location}</p>
      </div>
    </Link>
  );
};

export default PortfolioCard;