"use client";

import { Dispatch, SetStateAction, useEffect } from 'react';
import AOS from 'aos';
import 'aos/dist/aos.css';
import { useTranslation } from '@/lib/i18n/client';
import { isEven } from '@/utils';
import { Tab, TabList } from "@headlessui/react"

interface Props {
  lng: string;
}

interface Service {
  key: string;
  icon: string;
}

export const TextWrapper = ({ lng }: Props) => {
  const { t } = useTranslation(lng, 'common');

  const services: Service[] = [
    {
      key: "costEffective",
      icon: '💰',
    },
    {
      key: 'expertiseAndInnovation',
      icon: '🛠️',
    },
    {
      key: "clientCentricApproach",
      icon: '🤝',
    },
    {
      key: "strategicLocation",
      icon: '🌎',
    },
  ];

  function getSide(index: number) {
    return isEven(index) ? 'left' : 'right';
  }


  useEffect(() => {
    AOS.init({ duration: 1000 });
  }, []);

  return (
    <TabList className="flex flex-col items-center gap-6">
      <div
        data-aos={'fade-right'}
      >
        <Tab
          className={`data-[hover]:scale-110 chat-bubble right glow-effect text-black`}
        >
          Collaborate with LOM Tech
        </Tab>
      </div>
      {services.map((item, index) => (
        <div
          key={index}
          data-aos={`fade-${getSide(index)}`}
        >
          <Tab
            className={`data-[hover]:scale-110 chat-bubble ${(getSide(index))} glow-effect text-black`}
          >
            {item.icon} {t(`whyLomTech.${item.key}.title`)}
          </Tab>
        </div>
      ))}
    </TabList>
  );
};
