import { FC } from "react";

export interface Props {
    title: string;
    points:
    { title: string, description: string }[]

}

export const Template: FC<Props> = ({
    title,
    points
}) => {
    return (
        <div>
            <span className="text-3xl text-white font-bold">{title}</span>
            <ul className="flex flex-col gap-5 my-5">
                {points.map(({ title, description }) => <li key={title}><span className="text-2xl text-white">{title}</span><span className="mx-1">:</span><span>{description}</span></li>)}
            </ul>
        </div>
    )
}